@charset "utf-8";
html{-webkit-text-size-adjust:100%;-webkit-tap-highlight-color:rgba(0,0,0,0)}
a,img{-webkit-touch-callout:none;border:0}
blockquote,body,dd,dl,figure,form,h1,h2,h3,h4,h5,h6,hr,input,ol,p,td,textarea,th,ul{margin:0;padding:0}
h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:400}
li{list-style:none}
table{border-collapse:collapse;border-spacing:0}
button:focus,input:focus,textarea:focus{outline:0}
a,a:hover{text-decoration:none}
button,input,select,textarea{line-height:inherit;font-family:inherit;font-weight:inherit;font-size:100%}
body{font:400 .12rem/1.5 -apple-system,sans-serif;}

/*文本是否可选*/
.selectable{-webkit-user-select:none;}
body,html {
    -webkit-font-smoothing: antialiased; 
    -moz-osx-font-smoothing: grayscale;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

