/*设定页面编码方式*/
@charset "utf-8";

/* ↓↓↓↓↓↓↓以下为必须引入的样式模块 ↓↓↓↓↓↓↓*/
/*rem单位计算*/
@import "rem_width.import.css";

/*引用公用样式*/
@import "global.import.css";
/* ↑↑↑↑↑以上为必须引入的样式模块 ↑↑↑↑↑↑↑*/

