    font-family: 'aksb';
    src: url('./font/Akrobat-SemiBold.woff') format('woff');
    font-style: normal;
    font-weight: 400;
} */

@font-face {
    font-family: 'Gilroy';
    src: url('data:font/woff;charset=utf-8;base64,') format('woff');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
} 

/*主内容样式*/
/* body{background-color:#081633;color:#D9BF10;} */
body{background-color:#000;color:#fff;}
.page_loading{position:absolute;top:0;left:0;width:100%;height:100%;display:-webkit-box;-webkit-box-orient:vertical;-webkit-box-pack:center;-webkit-box-align:center;z-index:1001;background-color:#000;color:#fff;-webkit-pointer-events:auto;pointer-events:auto;opacity:1;-webkit-transition:opacity 0.7s cubic-bezier(0.8,0,0.2,1);}
.page_loading.loaded{opacity:0;-webkit-pointer-events:none;pointer-events:none;}
.page_loading__text{font-size:0.16rem;text-align:center;}
.page_loading__num{margin-right:0.03rem;}
.wrap{position:relative;height:100vh;background-color:#000;}
.scene,
.slider_box{position:absolute;top: 0;left: 0;z-index: 3;width: 100%;height: 100vh;}
/* .scene{opacity:0;} */
.slider_box{z-index:11;}
/* .frame{position:absolute;left:0;top:0;right:0;height:0.4rem;z-index:51;} */
.frame_logo{position:absolute;left:0.24rem;top:0.2rem;width:0.36rem;height:0.36rem;z-index:81;mix-blend-mode: difference;}
.frame_title,
.frame_title__logo{display:block;width:0.36rem;height:0.36rem;font-size:0;fill:#fff;stroke:none;}
.frame_nav{position:absolute;right:0.18rem;top:0.12rem;width:0.36rem;height:0.36rem;z-index:501;mix-blend-mode: difference;}
.frame_menu{position:relative;width:0.36rem;height:0.36rem;display:-webkit-box;-webkit-box-orient:vertical;-webkit-box-pack:center;-webkit-box-align:center;}
.frame_menu__shape{position:relative;width:0.24rem;height:0.1rem;}
.frame_menu__shape::before,
.frame_menu__shape::after{position:absolute;content:"";right:0;width:100%;height:0.03rem;background-color:#fff;will-change:transform;transition:all .3s cubic-bezier(.23,1,.32,1) 0.1s;transform-origin:center;border-radius:0.1rem;}
.frame_menu__shape::before{top:0;width:100%;}
.frame_menu__shape::after{top:100%;width:60%;}
.frame_menu.open .frame_menu__shape::before{top:50%;transform:rotate(45deg);}
.frame_menu.open .frame_menu__shape::after{top:50%;transform:rotate(-45deg);width:100%;}

/*portfolio*/
.page{position:absolute;top:0;left:0;width:100vw;height:100vh;z-index:21;}
.page_title{position:relative;z-index:11;height:100%;}
.page_title__section{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);display:-webkit-box;-webkit-box-orient:horizontal;-webkit-box-align:center;white-space:nowrap;}
.page_title__line{font-size:0.4rem;text-align:center;font-family:"akb";line-height:1.25;overflow:hidden;}
.out .char{-webkit-transform:translateY(100%);}
/* .page_cover::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;display:block;background-image:-webkit-linear-gradient(top,rgba(0,0,0,0.5),rgba(0,0,0,0.2) 20%,rgba(0,0,0,0.2) 80%,rgba(0,0,0,0.5));-webkit-pointer-events:none;pointer-events:none;} */
.page_cover::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;display:block;background:rgba(0,0,0,.15);-webkit-pointer-events:none;pointer-events:none;}

/*project*/
.project_page{position:relative;width:100vw;height:100vh;overflow:auto;z-index:35;}
.project_intro__tit,
.project_intro__desc{margin-bottom:0.4rem;}
.project_start_title{position:absolute;left:20%;right:20%;top:0.25rem;z-index:21;display:-webkit-box;-webkit-box-orient:horizontal;-webkit-box-align:center;-webkit-box-pack:center;font-size:0.25rem;font-family:"akb";overflow:hidden;height:0.28rem;}
.project_bg{position:absolute;top:0;left:0;width:100%;height:100%;display:block;z-index:31;}
.project_bg__img{display:block;width:100%;height:100%;object-fit:cover;}

.page_content{width:100vw;overflow:hidden;background-color:#000;}
.page_start{position:relative;height:100vh;}
.project_intro{position:relative;padding:0.3rem 0.3rem 0.6rem;font-size:0.14rem;}
.project_intro__tit{font-size:0.2rem;font-family:"akb";}
.project_intro__para{margin-bottom:0.15rem;}
.project_intro__desc{margin-bottom:0.4rem;}
.project_intro__data{display:-webkit-flex;margin-bottom:0.1rem;}
.project_intro__data_side{width:0.5rem;}
.project_intro__data_side_txt{letter-spacing:.02rem;-webkit-writing-mode:vertical-rl;writing-mode:vertical-rl;line-height:1;}
.project_intro__data_con{-webkit-box-flex:1;}
.project_intro__data_unit{margin-bottom:0.05rem;}

.project_intro__data_el{flex:1;overflow:hidden;}
.project_intro__data_tit{font-family:"akb";padding-bottom:0.05rem;}
.project_intro__desc,
.project_intro__data_subtxt{opacity:0.5;}

.project_part{padding:0.3rem 0;}
.project_part__title{padding:0 0.3rem 0.4rem;}
.project_part__title_no{font-size:0.12rem;letter-spacing:1px;}
.project_part__title_con{font-size:0.18rem;font-family:"akb";line-height:1.2;}
.mobile_layout{padding:0 .3rem;}

.layout_picture{perspective:10rem;padding:0 .3rem;}
.layout_thumb{position:relative;overflow:hidden;margin-bottom:0.3rem;}
.layout_thumb_img{width:100%;display:block;object-fit:cover;}
.layout_thumb.landscape .layout_thumb_img,
.layout_thumb.portrait .layout_thumb_img,
.layout_thumb.wide .layout_thumb_img,
.layout_thumb.middle .layout_thumb_img,
.layout_thumb.narrow .layout_thumb_img{position:absolute;top:0;left:0;height:100%;}
.layout_thumb.landscape::after,
.layout_thumb.portrait::after,
.layout_thumb.wide::after,
.layout_thumb.middle::after,
.layout_thumb.narrow::after{content:"";display:block;}
.layout_thumb.landscape,
.layout_thumb.landscape::after{padding-top:46.18%;}
.layout_thumb.portrait::after{padding-top:216.53%;}
.layout_thumb.wide::after{padding-top:56%;}
.layout_thumb.middle::after{padding-top:100%;}
.layout_thumb.narrow::after{padding-top:177.87%;}
.layout_thumb_el{position:absolute;left:50%;top:50%;}
.scaleup::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;display:block;background-color: #111;-webkit-transition:0.4s ease;z-index:5;-webkit-pointer-events:none;pointer-events:none;}
.scaleup.animated::before{height:0;}
.scaleup img{-webkit-transform:scale(1.3) translateZ(-5px);-webkit-transition: opacity 0.3s cubic-bezier(0.22, 0.61, 0.36, 1),-webkit-transform 0.9s cubic-bezier(0.22, 0.61, 0.36, 1) 0.15s;opacity:0;}
.scaleup.animated img{-webkit-transform:scale(1);opacity:1;}

/*视频样式覆盖*/
.video-react .video-react-big-play-button{width:1.5em !important;border-radius:9rem !important;left:50% !important;top:50% !important;margin-left:-.75em !important;margin-top:-.75em !important;}
.video-react .video-react-big-play-button:before{top:-1px !important;}

.case_box{padding:0 0.3rem;}
.case_item{position:relative;width:100%;margin-bottom:0.3rem;}
.case_item_preview{position:relative;overflow:hidden;}
.case_item_preview__img{position:relative;margin:-.3rem 0;display:block;width:100%;/*height:6.82rem;*/}
.case_item__desc{position:relative;z-index:11;margin-bottom:0.3rem;}
.case_item_no{font-size:0.12rem;letter-spacing:1px;}
.case_item_name{font-size:0.18rem;font-family:"akb";line-height:1.2;}
.case_box .scaleup::before{background-color:#fff;}
.project_page--qqmusic .page_content{color:#000;background-color:#fff;}

.project_view{position:relative; background-color:#093bff;color:#fff;padding:0.3rem 0;height:4.4rem;}
.project_view__img{position:absolute;left:50%;top:0.3rem;display:block;width:4.4rem;height:4.4rem;margin-left:-2.2rem;z-index:11;}
.project_view__bg{position:absolute;left:0.3rem;top:50%;height:2rem;margin-top:-1rem;}
.project_view__bg_txt{display:block;width:2rem;height:2rem;fill:none;stroke:currentColor;stroke-width:0.2;opacity:0.5;}
.proect_view__bg_con{position:absolute;left:0.3rem;top:0.3rem;}
.proect_view__bg_no{white-space:nowrap;font-size:0.18rem;letter-spacing:1px;font-family:"akb";}
.proect_view__bg_name{white-space:nowrap;font-size:0.24rem;font-weight:bold;line-height:1.2;}

.project_spark{position:relative;padding-top:0.9rem;overflow:hidden;margin-bottom:0.3rem;}
.project_spark__view{position:relative;}
.project_spark__con{position:absolute;left:20px;top:20px;}
.project_spark_no{white-space:nowrap;font-size:0.18rem;letter-spacing:1px;font-family:"akb";}
.project_spark_name{white-space:nowrap;font-size:0.24rem;font-family:"akb";line-height:1.2;}
.project_spark__view{width:130vw;margin-left:-15vw;display:-webkit-box;-webkit-box-orient:horizontal;-webkit-box-align:end;}
.project_spark__view_el{padding:0 0.06rem;-webkit-box-sizing:border-box;box-sizing:border-box;width:20%;height:1.85rem;}
.project_spark__view_el--3{}
.project_spark__view_el--2,
.project_spark__view_el--4{margin-bottom:-20px;}
.project_spark__view_el--1,
.project_spark__view_el--5{margin-bottom:-40px;}
/* .project_spark__view_el--3{width:30%;}
.project_spark__view_el--2,
.project_spark__view_el--4{width:20%;}
.project_spark__view_el--1,
.project_spark__view_el--5{width:15%;} */
.project_spark__img{display:block;width:100%;}

.brand_part{position:relative;overflow:hidden;width:4.93rem;height:1.2rem;background-color:#000;padding:0.3rem 0;}
.brand_part__logo{position:absolute;left:0;top:50%;margin-top:-.5rem;display:block;fill:#fff;stroke:none;width:4.93rem;height:1rem;}
.slogen_part{position:relative;overflow:hidden;height:0.7rem;background-color:#fff;margin-bottom:.3rem;}
.slogen_part__wrap{position:absolute;left:0;top:0;font-size:0.6rem;font-family:"Gilroy";color:#000;line-height:0.7rem;display:-webkit-box;-webkit-box-orient:horizontal;-webkit-box-align:center;-webkit-animation:looptext 5s linear infinite both;}
.slogen_part__item{margin-right:0.1rem;}
@keyframes looptext{
    0%{-webkit-transform:translate3d(0,0,0);}
    100%{-webkit-transform:translate3d(-2.88rem,0,0);}
}
.slogen_part--hiphop{padding-bottom:.4rem;margin:0 -.3rem;}
.slogen_part--hiphop{background:none;}
.slogen_part--hiphop .slogen_part__wrap{-webkit-animation:looptext_hiphop 5s linear infinite both;-webkit-text-stroke:1px #00f4c5;color:transparent;}

@keyframes looptext_hiphop{
    0%{-webkit-transform:translate3d(0,0,0);}
    100%{-webkit-transform:translate3d(-2.24rem,0,0);}
}


/*画廊tiles*/
.tiles{height:100vh;position:relative;overflow:hidden;}
.tiles_wrap{width:150%;height:7rem;position:absolute;left:50%;top:50%;-webkit-transform:translate3d(-50%,-50%, 0);}
.tiles_line{display:flex;}
.tiles_line_img{flex:none;width:1.5rem;height:2.2rem;margin:0.2rem 0;background-size:cover;background-position:50% 50%;}
.tiles--columns{height:8.4rem;}
.tiles--columns .tiles_line{width:2.4rem;flex:none;padding:0 0.1rem;display:block;}
.tiles--columns .tiles_line_img{width:100%;height:1.2rem;}
.tiles--columns .tiles_wrap{width:150%;display:flex;-webkit-transform:translate3d(-50%,-50%,0) rotate(-22.5deg);left:50%;top:50%;align-items:center;justify-content:center;}
.tiles--perspective{overflow:visible;perspective:1200px;}
.tiles--perspective .tiles_wrap{width:200%;-webkit-transform:translate3d(-50%,-50%,0) translateZ(100px) rotateX(55.5deg) rotateZ(8deg);}
.tiles--perspective .tiles_line_img{margin:0.08rem 0.05rem;}
  
/*研究*/
.grid{position:absolute;top:0;left:0;width:100vw;height:100vh;display:block;background-color:#000;overflow:hidden;}
.grid_main{position:absolute;top:0;left:0;width:100%;height:100%;display:block;opacity:0;-webkit-pointer-events:none;pointer-events:none;z-index:35;-webkit-transition:opacity 0.3s cubic-bezier(0.8,0,0.2,1);}
.grid.under .grid_main{z-index:21;}
.grid_main__hd{position:absolute;left:0;top:0.7rem;z-index:31;-webkit-pointer-events:none;pointer-events:none;}
.grid_htitle{position:relative;font-size:0.3rem;font-family:"akb";padding-left:0.24rem;padding-right:0.1rem;height:.33rem;line-height:.33rem;overflow:hidden;}
.grid_htitle__inner{display:block;}
.grid_main__hd_line{position:absolute;left:0;width:100%;height:1px;overflow-x:hidden;}
.grid_main__hd_line_inner{display:block;height:1px;width:100%;background-color:currentColor;opacity:0.7;}
.grid_main.top{z-index:35;}
.grid_item{position:absolute;left:50%;top:50%;width:0.8rem;height:0.8rem;}
.grid_item_thumb{position:absolute;top:0;left:0;width:100%;height:100%;display:block;background-size:cover;background-position:50%;}
.grid_detail{position:absolute;top:0;left:0;right:0;bottom:0;z-index:31;color:#000;}
.grid_detail__wrap{position:absolute;top:0;left:0;right:0;bottom:0;display:block;overflow:auto;-webkit-overflow-scrolling:touch;padding:0.3rem;-webkit-box-sizing:border-box;box-sizing:border-box;}
.grid_detail__hd{position:relative;width:3.15rem;height:3.15rem; margin-bottom:0.2rem;margin-top:0.45rem;}
.grid_detail_subtit{font-size:0.16rem;}
.grid_detail_title{font-size:0.3rem;font-weight:bold;line-height:1.2;}
.grid_detail_part{margin-top:0.3rem;}
.grid_detail_para{font-size:0.14rem;margin-bottom:0.15rem;}
.grid_detail_view{display:-webkit-box;-webkit-box-orient:horizontal;-webkit-box-align:center;}
.grid_detail_thumb{position:relative; -webkit-box-flex:1;display:block;object-fit:cover;background-color:#000;}
.grid_detail_thumb__img{display:block;width:100%;}
.grid_detail_thumb.loading::after{content:"";position:absolute;left:50%;top:50%;width:0.36rem;height:0.36rem;border:2px solid rgba(255,255,255, 0.1);border-right: 2px solid #fff;border-radius:50%;-webkit-animation:spin 1s linear infinite;margin-left:-0.18rem;margin-top:-0.18rem;}
@keyframes spin {
    100% {
      -webkit-transform:rotate(360deg);
    }
}
.grid_detail_thumb.loading .grid_detail_thumb__img{opacity:0;}
.grid_act{position:absolute;left:50%;top:0.24rem;width:.32rem;height:.32rem;margin-left:-.16rem;z-index:41;mix-blend-mode:difference;-webkit-pointer-events:auto;pointer-events:auto;-webkit-transition:opacity 0.5s 0.2s cubic-bezier(0.8,0,0.2,1);}
.grid_act_close{color:inherit;display:block;}
.grid_act_close__icon{display:block;width:0.32rem;height:0.32rem;fill:currentColor;stroke:none;}
.grid_detail .grid_item{position:static;height:100%;width:100%;z-index:31;}

.grid_detail_subtit,.grid_detail_title,.grid_detail_article{opacity:0;}
.grid--open .grid_main{opacity:1;-webkit-pointer-events:auto;pointer-events:auto;-webkit-transition:none;}
.grid--open .grid_detail{opacity:0;}
.grid--open .grid_act{opacity:0;-webkit-transition:none;-webkit-pointer-events:none;pointer-events:none;}

/*向下滚动*/
.project_scroll{position:absolute;left:50%;width:0.6rem;bottom:0;z-index:41;margin-left:-0.3rem;}
.project_scroll__text{display:block;text-align:center;white-space:nowrap;font-size:0.16rem;font-family:"akb";}
.project_scroll__line{display:-webkit-box;-webkit-box-orient:horizontal;-webkit-box-pack:center;overflow:hidden;}
.project_scroll__line_el{display:block;width:0.02rem;height:0.25rem;background-color:#fff;border-radius:0.1rem;}


/*滚动条区域*/
.page_slidershow{position:absolute;top:0;left:0;width:100vw;height:100vh;z-index:55;}
.scrollarea{position:absolute;top:0;left:0;width:100vw;height:100vh;display:flex;flex-flow:column nowrap;justify-content:center;z-index:51;}
/* .scroll-content{width:100%;height:80vh;max-height:5.6rem;-webkit-box-sizing:border-box;box-sizing:border-box;padding:0.2rem;} */
/* .scroll-content{width:100%;height:100vh;} */
.slidershow_list{display:flex;padding-top:0.5rem;}
.slidershow_tile{position:relative;}
.slidershow_link{display:block;color:#fff;}
.slidershow_item{margin-left:0.5rem;}
.slidershow_list .slidershow_item:first-child{margin-left:0.3rem;}
.slidershow_list .slidershow_item:last-child{padding-right:0.3rem;}
.slidershow_fig{position:relative;width:27.7vh;height:60vh;}
.slidershow_img{position:absolute;top:0;left:0;width:100%;height:100%;display:block;object-fit:cover;object-position:center;transition:opacity 0.3s;}
.slidershow_tile__content{position:relative;font-size:0.2rem;font-family:"akb";width:100%;z-index:11;margin-top:0.2rem;}
.slidershow_tile__no,
.slidershow_tile__title,
.slidershow_tile__subtxt{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;-webkit-transition:opacity 0.5s cubic-bezier(0.8,0,0.2,1);opacity:1;}
.slidershow_tile__no{font-size:0.16rem;font-family:"akb";margin-bottom:0.1rem;-webkit-transition-delay:0.05s;}
.slidershow_tile__title{font-size:0.2rem;font-weight:500;-webkit-transition-delay:0.08s;}
.slidershow_tile__subtxt{font-size:0.14rem;font-weight:400;opacity:0.7;-webkit-transition-delay:0.1s;}
.slideshow_progress{position:absolute;left:15vw;width:70vw;bottom:0.3rem;height:1px;background-color:rgba(255,255,255,.2);overflow:hidden;}
.slideshow_progress__inner{position:absolute;left:0;top:0;width:100%;height:100%;background-color:#fff;-webkit-transform:translateX(-100%);}
.moving .slidershow_tile__no,
.moving .slidershow_tile__title,
.moving .slidershow_tile__subtxt{opacity:0;-webkit-transition:none;}
.slidershow_np{position:absolute;top:0;left:0;width:100%;height:100%;display:-webkit-box;-webkit-box-pack:center;-webkit-box-align:center;background-color:rgba(0,0,0,.7);color:#fff;font-size:0.24rem;}
.slidershow_np__arrow{display:block;width:0.24rem;height:0.24rem;fill:currentColor;stroke:none;}

/*首页内容*/
.process_round{position:absolute;right:0.3rem;bottom:0.3rem;width:.4rem;height:.4rem;z-index:101;display:none;opacity:0;}
.process_round__svg{position:relative;display:block;width:100%;height:100%;}
.process_round__path_bg,
.process_round__path{position:absolute;top:0;left:0;width:100%;height:100%;display:block;fill:none;stroke:#fff;stroke-miterlimit:10;stroke-width:2px;}
.process_round__path_bg{opacity:0.1;}
.process_round__path{z-index:11; stroke-dasharray:158px,158px;stroke-dashoffset: 138;}
.home_pagination{position:absolute;left:0.25rem;bottom:0.25rem;z-index:101;display:-webkit-box;-webkit-box-orient:horizontal;-webkit-box-align:center;font-family:"akb";font-size:0.16rem;line-height:0.18rem;}
.home_pagination_current{position:relative; width:0.08rem;height:0.18rem;overflow:hidden;}
.home_pagination_el{position:absolute;left:0;top:0;width:100%;height:.18rem;}
.home_pagination_line{position:relative;height:2px;width:0.06rem;margin:0 0.05rem 0 0.03rem;overflow:hidden;}
.home_pagination_line_el{position:absolute;left:0%;height:100%;width:100%;background-color:#fff;border-radius:0.1rem;}

.project_page--fanlive{background-color:#000;}


.anim_pane{position:relative;color:#000;height:100vw;overflow:hidden;}
.anim_desc{position:absolute;z-index:11;}
.anim_desc_title{font-weight:700;line-height:1.2;font-size:0.32rem;}
.anim_desc_text{font-size:0.2rem;font-family:"akb";}
.ani_switch{display:-webkit-box;-webkit-box-orient:horizontal;-webkit-box-align:center;-webkit-box-pack:center;font-size:0.18rem;margin-top:0.2rem;}
.swith_btn{position:relative;width:0.8rem; height:0.36rem;border:0.03rem solid;margin:0 0.1rem;border-radius:9rem;}
.swith_btn::after{content:"";position:absolute;left:0.05rem;top:0.05rem;width:0.26rem;height:0.26rem;border-radius:9rem;background-color:currentColor;-webkit-transition:-webkit-transform 0.5s cubic-bezier(0.8,0,0.2,1);}
.swith_btn.checked::after{-webkit-transform:translateX(0.45rem);}

.anim_pane__el,
.anim_pane__canvas{position:relative;width:100%;height:100%;display:block;}
.anim_pane--banner{height:117.33vw;padding-top:1.2rem;background-color:#fbd034;}
/* .anim_pane--banner{height:117.33vw;padding-top:1.2rem;background-color:transparent;} */

.anim_pane--banner .anim_desc{left:0.24rem;top:0.4rem;}
.anim_pane--star{height:128vw;padding-bottom:1rem;background-color:#feb6cc;}

.anim_pane--star .anim_desc{right:0.24rem;bottom:0.4rem;text-align:right;}
.anim_pane--element{padding-top:1rem;background-color:#405df6;color:#fff;}

.anim_pane--element .anim_desc_text{font-size:0.16rem;margin-top:0.05rem;}
.anim_pane--element .anim_desc{left:0;width:100%;top:0.4rem;text-align:center;}
.anim_pane--ship{background-color:#ddeeff;height:1.2rem}
.anim_pane--ship .anim_pane__el,
.anim_pane--ship .anim_pane__canvas{margin:-.5rem 0;height:2.8rem;}
.anim_pane--day .anim_desc{left:0;width:100%;top:0.4rem;text-align:center;}
.anim_pane--day{padding-top:1.4rem;background:#fff;-webkit-transition:all 0.5s cubic-bezier(0.8,0,0.2,1);}
.anim_pane--day.changed{background-color:#000;color:#fff;}
.anim_pane--day .anim_desc_text{font-size:0.16rem;margin-top:0.05rem;}
.anim_pane--submarine{padding:.35rem 0;background-color:#35cb8b;color:#000;}
.anim_pane--submarine .anim_desc{left:0;width:100%;top:0.4rem;text-align:center;}
.anim_pane--submarine .anim_pane__el,
.anim_pane--submarine .anim_pane__canvas{height:100vw;}
.anim_qrcode{position:absolute;left:50%;width:1.2rem;height:1.3rem;text-align:center;font-size:0.1rem;background-color: #fff;color:rgba(0,0,0,0.5);z-index:11;white-space:nowrap;padding-top:0.1rem;margin-left:-.6rem;bottom:.2rem;}
.anim_qrcode__img{display:block;width:1rem;height:1rem;margin:0.05rem auto 0;}

.next_project{position:relative;height:2rem;display:-webkit-box;-webkit-box-orient:vertical;-webkit-box-pack:center;}
.next_project__con{position:relative;z-index:11;height:0.4rem;line-height:.4rem;perspective:200px;-webkit-perspective:200px;}
.next_project__title{font-size:0.36rem;color:#fff;text-align:center;font-family:"akb";}
.next_project::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;display:block;z-index:5;background-color:rgba(0,0,0,.6);}
.next_project__bg{position:absolute;top:0;left:0;width:100%;height:100%;display:block;background-repeat:no-repeat;background-size:cover;background-position:50%;}

/* .next_project__title{-webkit-transform:translateY(45px) rotate(5deg);-webkit-transition:-webkit-transform 0.5s cubic-bezier(0.8,0,0.2,1);} */
.next_project__title{-webkit-transform:translate3d(0,20px,30px) rotateX(-20deg);-webkit-transition:all 1.5s 0.5s cubic-bezier(.19,1,.22,1);opacity:0;}
.animated .next_project__title{-webkit-transform:none;opacity:1;}

/*menu菜单*/
.menu_box{}
.menu_wrap{position:absolute;top:0;left:0;width:100%;height:100%;z-index:310;color:#fff;font-family:'akb';padding:0.3rem;-webkit-box-sizing:border-box;box-sizing:border-box;opacity:0;-webkit-pointer-events:none;pointer-events:none;background-color:#000;}
.menu_wrap--open{opacity:1;-webkit-pointer-events:auto;pointer-events:auto;}
.menu_nav{padding-top:0.4rem;}
.page_overlay{position:absolute;top:0;left:0;width:100%;height:100%;display:block;z-index:1001;-webkit-pointer-events:none;pointer-events:none;}
.page_overlay_path{fill:#000;stroke:none;;}
.menu_item{display:-webkit-box;-webkit-box-orient:horizontal;-webkit-box-align:top;color:inherit;margin-bottom:0.1rem;position:relative;}
.menu_item__text{display:block;font-size:0.36rem;margin-right:0.05rem;line-height:1.1;}
.menu_item__id{display:block;font-size:0.14rem;color:#1651fa;}

.home_view_project{position:absolute;left:30%;right:30%;bottom:.2rem;display:-webkit-box;-webkit-box-orient:horizontal;-webkit-box-align:center;-webkit-box-pack:center;z-index:31;}
.home_view_project__con{display:-webkit-box;-webkit-box-orient:horizontal;-webkit-box-align:center;font-size:0.18rem;font-family:"akb";color:#fff;}
.home_view_project__arrow{display:block;width:0.23rem;height:0.23rem;fill:currentColor;stroke:none;}

/*videos*/
.project_page--videos .page_content{padding-top:1rem;}
.page_video{position:relative;perspective:10rem;-webkit-perspective:10rem;margin-bottom:0.2rem;}
.page_video .video-react{-webkit-transform:translate3d(0,20px,30px) rotateX(-20deg);opacity:0;-webkit-transition:all 1.5s 0.2s cubic-bezier(.19,1,.22,1);}
.page_video.animated .video-react{-webkit-transform:none;opacity:1;}
.project_page--videos .project_intro{-webkit-transform:translate3d(0,20px,30px) rotateX(-20deg);opacity:0;-webkit-transition:all 1.5s 0.5s cubic-bezier(.19,1,.22,1);}
.project_page--videos .project_intro.animated{-webkit-transform:none;opacity:1;}

/*about*/
.about_box{position:relative;height:100vh;-webkit-box-sizing:border-box;box-sizing:border-box;overflow:auto;-webkit-overflow-scrolling:touch;}
.about_wrap{position:relative;padding-top:1rem;z-index:11;}
.about_avatar{display:block;width:100%;height:2.5rem;}
.about_avatar__img{display:block;width:100%;height:2.5rem;opacity:0;}
.about_name{text-align:center;font-size:0.16rem;color:#fff;font-family:"akb";}
.about_bg{position:absolute;top:0;left:0;width:100vw;height:100vh;display:block;}



.is-loaded{opacity:0;}

/* 初始化样式 */
.page_title__group,
.home_pagination,
.home_view_project{opacity:0;}

